.App {
  text-align: center;
}
body{
  background-color: #292929;
  color:  white;
  font-size: 20px;
}
.App{
  width: 500px;
  margin: 0 auto;
}
form{
  display: flex;
  justify-content: space-between;
  text-align: left;
}

input{
  background: none ;
  border: none;
  font-size: 20px;
  color: white;
}
button{
  background: none;
  border: none;
  color: #fff;
  font-size: 30px
}
.item-todo{
  display: flex;  
  text-align-last: left;
  justify-content: space-between;
  cursor: pointer;
  padding: 6px 0;
  margin-top: 14px;  
}
.item-text{
  display: inline-block;
  color: white;
  font-size: 20px;

}
.strike{
  text-decoration: line-through;
  color: green;
}